<template>
  <li class="catalog__item">
    <RouterLink
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.slug } }"
    >
      <img
        :src="imgUrls[currentColorIndex] || placeholderImg"
        :alt="product.title"
      />
    </RouterLink>

    <h3 class="catalog__title">
      <a href="#"> {{ product.title }} </a>
    </h3>

    <span class="catalog__price"> {{ product.price | numberFormat }} ₽ </span>

    <div class="catalog__item-bottom">
      <ul class="colors colors--black">
        <li
          class="colors__item"
          v-for="(colorData, index) of product.colors"
          :key="colorData.color.id"
        >
          <label class="colors__label">
            <input
              class="colors__radio sr-only"
              type="radio"
              :value="colorData.color.code"
              :name="product.id + 'color'"
              :checked="index === currentColorIndex"
              @click="setCurrentColorIndex(index)"
            />
            <span
              class="colors__value"
              :style="{ backgroundColor: colorData.color.code }"
            ></span>
          </label>
        </li>
      </ul>

      <select class="catalog__item-sizes" name="sizes" v-model="currentSizeId">
        <option
          v-for="size in sizes"
          :value="size.id"
          :key="size.id + product.title"
        >
          {{ size.title }}
        </option>
      </select>

      <button
        class="catalog__item-buy"
        @click.prevent="putToCart"
        :disabled="isLoading && !productData"
      >
        <svg v-if="isLoading" class="icon-preloader" width="32" height="32">
          <use xlink:href="#icon-preloader"></use>
        </svg>
        <svg v-else width="32" height="32">
          <use xlink:href="#icon-bag"></use>
        </svg>
        <span class="visually-hidden">Добавить в корзину</span>
        <span class="catalog__btn-amount" v-if="!isLoading">{{
          productCartCount
        }}</span>
      </button>
    </div>
  </li>
</template>

<script>
import numberFormat from "@/helpers/number-format";
import { RouterLink } from "vue-router";
import placeholderImgMixin from "@/mixins/placeholderImgMixin";
import Api from "@/helpers/api";
import { showHandledMessage } from "@/notifications";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "ProductItem",
  components: {
    RouterLink,
  },
  mixins: [placeholderImgMixin],
  props: ["product"],
  data() {
    return {
      currentColorIndex: 0,
      isLoading: false,
      productData: null,
      currentSizeId: 0,
    };
  },
  methods: {
    ...mapActions(["createCartProduct"]),
    setCurrentColorIndex(index) {
      this.currentColorIndex = index;
    },
    putToCart() {
      this.isLoading = true;
      this.createCartProduct({
        product: this.productData,
        productId: this.product.id,
        colorId: this.product.colors[this.currentColorIndex].color.id,
        sizeId: this.currentSizeId,
        amount: 1,
        onErrorCB: (error) => {
          this.isLoading = false;
          showHandledMessage(this, error);
        },
        onSuccessCB: () => {
          this.isLoading = false;
          showHandledMessage(this, { code: "PRODUCT_ADD" });
        },
      });
    },
  },
  computed: {
    ...mapGetters(["productsForCartPage"]),
    imgUrls() {
      const colors = this.product?.colors;
      if (colors && colors.length) {
        return colors.map((color) => {
          const gallery = color.gallery;
          if (gallery && gallery.length) {
            return gallery[0].file.url;
          }
        });
      }
      return [];
    },
    sizes() {
      return this.productData?.sizes ? this.productData.sizes : [];
    },
    productCartCount() {
      if (this.productsForCartPage?.length) {
        const count = this.productsForCartPage.filter(
          (item) => item.id === this.product.id
        ).length;
        return count > 0 ? count : "";
      }
      return "";
    },
  },
  filters: {
    numberFormat,
  },
  created() {
    this.isLoading = true;
    Api.getProductById(this.product.id)
      .then((response) => {
        this.productData = response.data;
        this.currentSizeId = response?.data?.sizes?.length
          ? response?.data?.sizes[0].id
          : 0;
      })
      .catch((error) => {
        showHandledMessage(this, error);
      })
      .finally(() => {
        this.isLoading = false;
      });
  },
};
</script>

<style lang="scss" scoped>
.catalog__item {
  display: flex;
  flex-direction: column;
}

.catalog__price {
  margin-top: auto;
}
.catalog__item-bottom {
  display: flex;
  justify-content: space-between;
}

.catalog__item-buy {
  position: relative;
  border: none;
  padding: 0;
  width: 32px;
  height: 36px;
  background-color: transparent;
  transition: 0.25s ease-in color, 0.25s ease-in opacity;
  cursor: pointer;

  &:focus-visible:not(:active),
  &:hover:not(:focus):not(:active) {
    color: #e02d71;
  }
  &:active {
    opacity: 0.5;
  }
  &:disabled {
    opacity: 0.2;
    cursor: not-allowed;
  }
}

.catalog__item-sizes {
  border: none;
}

.catalog__btn-amount {
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  color: #e02d71;
}

.icon-preloader {
  top: 5px;
  right: 3px;
  position: absolute;
  display: flex;
  align-items: center;
  width: 24px;
  height: 24px;
  animation: rotate 2s linear infinite;
}
</style>
