import { Messages } from "@/config";
import Vue from "vue";
import Notifications from "vue-notification";

Vue.use(Notifications);

export default Notifications;

export function showHandledMessage(context, error) {
  switch (error?.code) {
    case "ONLINE":
      context.$notify({
        text: Messages.ONLINE,
        type: "success",
      });
      break;
    case "ORDER":
      context.$notify({
        text: Messages.ORDER_SUCCESS,
        type: "success",
      });
      break;
    case "PRODUCT_ADD":
      context.$notify({
        text: Messages.PRODUCT_SUCCESS,
        type: "success",
      });
      break;
    case "PRODUCT_DELETE":
      context.$notify({
        text: Messages.PRODUCT_DELETED,
        type: "warn",
      });
      break;
    case "ERR_NETWORK":
      context.$notify({
        text: Messages.ERROR_NETWORK,
        type: "error",
      });
      break;
    case "OFFLINE":
      context.$notify({
        text: Messages.OFFLINE,
        type: "error",
      });
      break;

    default:
      context.$notify({
        text: Messages.ERROR_DEFAULT,
        type: "error",
      });
  }
}
